@tailwind base;
@tailwind components;
@tailwind utilities;

*{
  margin:0;
  padding:0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  text-transform:capitalize;
  text-decoration: none;
}

header{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #0d1728;
  box-shadow: 0 5px 10px rgba(0, 0, 0, -1);
  padding: 0px 7%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  
}

header .logo{
  font-weight: bolder;
  font-size: 25px;
}

header .navbar ul{
  list-style: none;
}

header .navbar ul li{
  position: relative;
  float: left;
}

header .navbar ul li a{
  font-size: 15px;
  padding: 25px;
  color: #fff;
  display: block;
}

header .navbar ul li a:hover{
  background: #fff;
  color: #0d1728;
}

header .navbar ul li ul{
  position: absolute;
  left: 0;
  width: 200px;
  background: #0d1728;
  display: none;
}

header .navbar ul li ul li{
 width: 100%;
 border-top: 1px solid rgba(0, 0, 0, .1);
}

header .navbar ul li ul li ul{
  left: 200px;
  top: 0;
 }
 
header .navbar ul li:hover > ul {
  display: initial;
}

#menu-bar{
  display: none;
}
header label{
  font-size: 20px;
  color: #333;
  cursor: pointer;
  display: none;
}

.barsImg{
  width: 45px;
  height: 45px;
}

.subtopImg{
  height: 300px;
  margin-top: 72.5px;
}
@media(max-width:1100px) {
  .subtopImg{
    height: 150px;
    margin-top: 85px;
  }
  header{
    padding: 20px;
  }

  header label{
    display: initial;
  }

  header .navbar{
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background: #0d1728;
    border-top: 1px solid rgba(0, 0, 0, .1);
    display: none;
  }

  header .navbar ul li{
    width: 100%;
  }

  header .navbar ul li ul{
    position: relative;
    width: 100%;
  }

  header .navbar ul li ul li{
   background: #333;
  }

  header .navbar ul li ul li ul{
    width: 100%;
    left: 0;
   }

   #menu-bar:checked ~ .navbar{
    display: initial;
   }
}